</i> Update my Profile Picture</a>\r\n  </div>\r\n</template>\r\n\r\n<style scoped>\r\n\r\n#presenter-promo {\r\n  position: relative;\r\n  width: 100%;\r\n  text-align:center\r\n}\r\n\r\n.conference-image {\r\n  width: 100%;\r\n}\r\n\r\n.profile-image {\r\n  width: 42%;\r\n  height: 34%;\r\n  object-fit: cover;\r\n  position: absolute;\r\n  bottom: 39.2%;\r\n  left: 51%;\r\n  transform: translateX(-51%);\r\n  border-radius: 50%;\r\n}\r\n\r\n.badge-name{\r\n  position: absolute;\r\n  top: 68%;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n  padding: 10px;\r\n  border-radius: 10px;\r\n  width: 85%;\r\n  text-align: center;\r\n  color: white;\r\n  font-size: 4.5rem;\r\n  font-family: 'Exo', sans-serif;\r\n  font-weight: bold;\r\n  text-shadow: 1px 1px 1px #000;\r\n  line-height: .8em;\r\n}\r\n\r\n\r\n@media (max-width: 1399px) {\r\n  .badge-name {\r\n    font-size: 3rem; /* Large screens */\r\n  }\r\n}\r\n\r\n@media (max-width: 990px) {\r\n  .badge-name {\r\n    font-size: 2.2rem; /* Medium screens */\r\n  }\r\n}\r\n\r\n\r\n@media (max-width: 799px) {\r\n  .badge-name {\r\n    font-size: 2.2rem; /* Small screens */\r\n  }\r\n}\r\n\r\n@media (max-width: 599px) {\r\n  .badge-name {\r\n    font-size: 2rem; /* Mobile screens */\r\n  }\r\n}\r\n</style>\r\n<script>\r\n// TODO - Make this a plugin and add admin features to customize image positions\r\nimport * as htmlToImage from 'html-to-image';\r\nimport { toJpeg } from 'html-to-image';\r\n\r\nexport default {\r\n  props: ['badgeName','profileImage', 'conferenceImage'],\r\n\r\n  methods: {\r\n    saveimage: function () {\r\n      htmlToImage.toJpeg(document.getElementById('presenter-promo'), { quality: 0.95 })\r\n          .then(function (dataUrl) {\r\n            var link = document.createElement('a');\r\n            link.download = 'presenter-promo.jpg';\r\n            link.href = dataUrl;\r\n            link.click();\r\n      });\r\n    },\r\n  },\r\n}\r\n</script>"],"sourceRoot":""}]);
